.menu__link {
  display: inline-block;
  box-sizing: border-box;
  padding: 16px 24px;

  font-weight: 600;
  font-size: 14px;
  color: #ffffff;
  text-decoration: none;

  background-color: #2e2e2e;
  border-radius: 8px;

  transition: color 0.2s ease;
}

.menu__link:hover {
  color: #9933ff;
}
