.button {
  display: flex;
  justify-content: center;
  align-items: center;

  box-sizing: border-box;
  padding: 16px 24px;

  font-weight: 500;
  font-size: 18px;
  color: #000000;

  background-color: #ffa500;
  border: none;
  border-radius: 4px;

  cursor: pointer;

  transition: 0.2s ease;
}

.button:hover {
  background-color: #e59100;
}
