.cards-switch__button {
  display: flex;
  justify-content: center;
  align-items: center;

  margin-right: 8px;
  padding: 8px;
  width: 32px;
  height: 32px;

  background-color: #2e2e2e;
  border: none;
  border-radius: 4px;
  fill: #ffffff;

  cursor: pointer;

  transition: fill 0.2s ease;
}

.cards-switch__button:last-child {
  margin-right: 0;
}
